import { step, sideEffect } from "@thi.ng/transducers";
import { subscription } from "@thi.ng/rstream";
export const particleIterator = (opts) => {
    let apply = step(opts.xform);
    let tmp = {
        type: "particle",
        data: null,
        clock: null,
    };
    return subscription({
        next: () => { },
        error: (err) => {
            throw err;
        },
    }, {
        // Apply given transducer to each particle in array.
        xform: sideEffect((ev) => {
            tmp.clock = ev.clock;
            for (let i = 0; i < ev.data.length; i++) {
                tmp.data = ev.data[i];
                apply(tmp);
            }
        }),
    });
};
